import * as React from "react";
import { Container, Row, Col, Navbar, Card, Nav, Offcanvas, Form, Carousel, Image, Button, Tab } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import '/src/css/custom.css';
import 'jquery/dist/jquery.min.js';
//import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import MultiCarousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import sectionHeaderIcon from '../images/deal.png';
import { graphql, useStaticQuery } from "gatsby";
import { Helmet } from "react-helmet";

// styles
const pageStyles = {
  color: "#232129",
  fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 3,
    slidesToSlide: 3 // optional, default to 1.
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 2,
    slidesToSlide: 2 // optional, default to 1.
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
    slidesToSlide: 1 // optional, default to 1.
  }
};


// markup
export default function App() {

// ----- Onload GraphQL Query Start -----//
  const data = useStaticQuery(graphql`
    query MyQuery {
      wpLogo {
        logoMeta {
          logo {
            sourceUrl
          }
        }
      }
      allWpBanner(
        sort: {fields: bannerMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            bannerMeta {
              imageUpload {
                sourceUrl
              }
            }
          }
        }
      }
      wpOverview {
        overviewMeta {
          lessDescription
          moreDescription
        }
      }
      allWpHighlight(
        sort: {fields: projectHighlightMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            projectHighlightMeta {
              description
              image {
                sourceUrl
              }
            }
          }
        }
      }
      allWpAmenitiesTab(
        sort: {fields: amenitiesTabsMeta___order, order: ASC}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            amenitiesTabsMeta {
              title
            }
          }
        }
      }
      allWpAmenitiesIcon(
        sort: {fields: amenitiesIconsMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            amenitiesIconsMeta {
              icon {
                sourceUrl
              }
              title
              tabsTitle
            }
          }
        }
      }
      allWpProjectTab(
        sort: {fields: projectTabMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            projectTabMeta {
              title
            }
          }
        }
      }
      allWpProjectImage(
        sort: {fields: projectImagesMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            projectImagesMeta {
              title
              tabTitle
              image {
                sourceUrl
              }
            }
          }
        }
      }
      allWpPrice(
        sort: {fields: priceContentMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            priceContentMeta {
              title
              area
              price
            }
          }
        }
      }
      wpContact {
        contactMeta {
          secondaryNumber
          primaryNumber
          mapLink
          email
          address
        }
      }
    }
  `)
// ----- Onload GraphQL Query End -----//

// ----- Logo -----//
  const logo = data.wpLogo.logoMeta.logo.sourceUrl

// ----- Slider Start -----//
  let slider = data.allWpBanner.edges
  let container = []
  slider.forEach((val, index) => {
    container.push(
      <Carousel.Item key={index}>
        <img
          className="d-block w-100"
          src={val.node.bannerMeta.imageUpload.sourceUrl}
          alt=""
        />
        <Carousel.Caption>
        </Carousel.Caption>
      </Carousel.Item>)
  });
// ----- Slider End -----//

// ----- Overview -----//
  const overview = data.wpOverview.overviewMeta

// ----- Highlight Start -----//
  let HighlightData = data.allWpHighlight.edges
  let Highlight = []
  HighlightData.forEach((val, index) => {
    Highlight.push(
      <div className="p-sm-2 p-md-3" key={index}>
        <Card className="p-2">
          <Card.Img variant="top" src={val.node.projectHighlightMeta.image.sourceUrl} />
          <Card.Body>
            <Card.Text>
              {val.node.projectHighlightMeta.description}
            </Card.Text>
          </Card.Body>
        </Card>
      </div>
    )
  });
// ----- Highlight End -----//

// ----- Amenities Start -----//
  let AmenitiesTabData = data.allWpAmenitiesTab.edges
  let AmenitiesIconData = data.allWpAmenitiesIcon.edges
  let AmenitiesTab = []
  let AmenitiesIcon = []
  let AmenitiesIconPanel = []
  AmenitiesTabData.forEach((val, index) => {
    AmenitiesTab.push(
      <Nav.Item key={index}>
        <Nav.Link eventKey={index}>{val.node.amenitiesTabsMeta.title}</Nav.Link>
      </Nav.Item>
    )
    AmenitiesIcon = []
    AmenitiesIconData.forEach((icon, iconIndex) => {
      if(val.node.amenitiesTabsMeta.title == icon.node.amenitiesIconsMeta.tabsTitle){
        AmenitiesIcon.push(
          <Col xs={6} sm={4} md={3} lg={2}>
          <Card className="p-2 mb-4">
            <Card.Img variant="top" src={icon.node.amenitiesIconsMeta.icon.sourceUrl} />
            <Card.Body>
              <Card.Text>
              {icon.node.amenitiesIconsMeta.title}
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
        )
      }
    });
    AmenitiesIconPanel.push(
      <Tab.Pane eventKey={index}>
        <Row>
          {AmenitiesIcon}
        </Row>
      </Tab.Pane>
    )
  });
// ----- Amenities End -----//

// ----- Plans Start -----//
let PlansTabData = data.allWpProjectTab.edges
let PlansImageData = data.allWpProjectImage.edges
let PlansTab = []
let PlansImage = []
let PlansImagePanel = []
PlansTabData.forEach((val, index) => {
  PlansTab.push(
    <Nav.Item key={index}>
      <Nav.Link eventKey={index}>{val.node.projectTabMeta.title}</Nav.Link>
    </Nav.Item>
  )
  PlansImage = []
  PlansImageData.forEach((image, imageIndex) => {
    if(val.node.projectTabMeta.title == image.node.projectImagesMeta.tabTitle){
      PlansImage.push(
        <div className="p-sm-2 p-md-3">
          <Card className="p-2">
            <Card.Img src={image.node.projectImagesMeta.image.sourceUrl} />
          </Card>
        </div>
      )
    }
  });
  PlansImagePanel.push(
    <Tab.Pane eventKey={index}>
      <MultiCarousel responsive={responsive}>
        {PlansImage}
      </MultiCarousel>
    </Tab.Pane>
  )
});
// ----- Plans End -----//

// ----- Price Start -----//
let PriceData = data.allWpPrice.edges
let Price = []
PriceData.forEach((val, index) => {
  Price.push(
    <div className="p-sm-2 p-md-3" key={index}>
      <Card className="pricing-card">
        <Card.Body>
          <h3 class="pricing-plan-title">{val.node.priceContentMeta.title}</h3>
          <h6>{val.node.priceContentMeta.area}</h6>
        </Card.Body>
        <Card.Footer>
          <a href="#!" class="card-link pricing-plan-purchase-btn">Get Price</a>
        </Card.Footer>
      </Card>
    </div>
  )
});
// ----- Price End -----//

// ----- Contact -----//
const contact = data.wpContact.contactMeta

  return (
    <main style={pageStyles}>
      <title>Prime Park</title>
      <Navbar bg="dark" expand={false} sticky="top">
        <Container>
          <Navbar.Brand href="#"><Image src={logo} width={150} /></Navbar.Brand>

          <ul className="list-group list-group-horizontal d-none d-lg-inline-flex">
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">Overview</a></li>
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">Highlights</a></li>
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">Amenities</a></li>
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">Location</a></li>
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">Plans</a></li>
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">Price</a></li>
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">Gallery</a></li>
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">Video</a></li>
            <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);">NRI Services</a></li>
          </ul>

          <Navbar.Toggle className="d-lg-none" aria-controls="offcanvasNavbar" />
          <Navbar.Offcanvas
            id="offcanvasNavbar"
            aria-labelledby="offcanvasNavbarLabel"
            placement="end"
          >
            <Offcanvas.Header closeButton>
              <Offcanvas.Title id="offcanvasNavbarLabel"><Image src="https://primepark.in/images/aboutlogo.png" width={150} /></Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>
              <Nav className="justify-content-end flex-grow-1 pe-3">
                <Nav.Link href="javascript:void(0);">Overview</Nav.Link>
                <Nav.Link href="javascript:void(0);">Highlights</Nav.Link>
                <Nav.Link href="javascript:void(0);">Amenities</Nav.Link>
                <Nav.Link href="javascript:void(0);">Location</Nav.Link>
                <Nav.Link href="javascript:void(0);">Plans</Nav.Link>
                <Nav.Link href="javascript:void(0);">Price</Nav.Link>
                <Nav.Link href="javascript:void(0);">Gallery</Nav.Link>
                <Nav.Link href="javascript:void(0);">Video</Nav.Link>
                <Nav.Link href="javascript:void(0);">NRI Services</Nav.Link>
              </Nav>
            </Offcanvas.Body>
          </Navbar.Offcanvas>
        </Container>
      </Navbar>
      <Carousel>
        {container}
      </Carousel>

      <Container fluid className="bg-white" id="overview">
        <Row className="section-heading mb-4 mx-md-5 mx-4">
          <img
            className="d-block m-auto mb-2"
            src={sectionHeaderIcon}
          />
          <h1 className="m-0">Overview</h1>
        </Row>
        <Row className="mx-md-5 mx-4 text-center">
          <p>{overview.lessDescription}</p>
          <p>{overview.moreDescription}</p>
        </Row>
      </Container>

      <Container fluid className="bg-light" id="highlights">
        <Row className="section-heading mb-4 mx-md-5 mx-4">
          <img
            className="d-block m-auto mb-2"
            src={sectionHeaderIcon}
          />
          <h1 className="m-0">Project Highlights</h1>
        </Row>
        <Row className="mx-md-5 mx-4 text-center">
          <Col>
            <MultiCarousel responsive={responsive}>
              {Highlight}
            </MultiCarousel>
          </Col>
        </Row>
      </Container>
      <Container fluid className="bg-white" id="amenities">
        <Row className="section-heading mb-4 mx-md-5 mx-4">
          <img
            className="d-block m-auto mb-2"
            src={sectionHeaderIcon}
          />
          <h1 className="m-0">Amenities</h1>
        </Row>
        <Row className="mx-md-5 mx-4 text-center">
          <Tab.Container defaultActiveKey="0">
            <Nav variant="pills">
              {AmenitiesTab}
            </Nav>
            <Tab.Content className="pt-5">
              {AmenitiesIconPanel}
            </Tab.Content>
          </Tab.Container>
        </Row>
      </Container>

      <Container fluid className="bg-light" id="plans">
        <Row className="section-heading mb-4 mx-md-5 mx-4">
          <img
            className="d-block m-auto mb-2"
            src={sectionHeaderIcon}
          />
          <h1 className="m-0">Project Plans</h1>
        </Row>
        <Row className="mx-md-5 mx-4 text-center">
          <Tab.Container defaultActiveKey="0">
            <Nav variant="pills">
              {PlansTab}
            </Nav>
            <Tab.Content className="pt-5">
              {PlansImagePanel}
            </Tab.Content>
          </Tab.Container>
        </Row>
      </Container>

      <Container fluid className="bg-white" id="price">
        <Row className="section-heading mb-4 mx-md-5 mx-4">
          <img
            className="d-block m-auto mb-2"
            src={sectionHeaderIcon}
          />
          <h1 className="m-0">Project Price</h1>
        </Row>
        <Row className="mx-md-5 mx-4 text-center">
          <Col>
            <MultiCarousel responsive={responsive}>
              {Price}
            </MultiCarousel>
          </Col>
        </Row>
      </Container>

      <Container fluid className="bg-light" id="contact">
        <Row className="section-heading mb-4 mx-md-5 mx-4">
          <img
            className="d-block m-auto mb-2"
            src={sectionHeaderIcon}
          />
          <h1 className="m-0">Contact Us</h1>
        </Row>

        <Row className="mx-md-5 mx-4">
          <Col xs={12} sm={12} md={12} lg={6} xl={6}>
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3966.521260322283!2d106.8195613507864!3d-6.194741395493371!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x2e69f5390917b759%3A0x6b45e67356080477!2sPT%20Kulkul%20Teknologi%20Internasional!5e0!3m2!1sen!2sid!4v1601138221085!5m2!1sen!2sid"
              frameBorder="0"
              allowFullScreen=""
              aria-hidden="false"
              tabIndex="0"
            />
          </Col>
          <Col xs={12} sm={12} md={12} lg={6} xl={6}>
            <Form className="input-form">
              <Row>
                <Col md={12}>
                  <h6>ENQUIRE NOW</h6>
                  <hr>
                  </hr>
                  <Form.Group controlId="name">
                    <Form.Control
                      type="text"
                      name="name"
                      placeholder="Name"
                      autoComplete="off"
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group controlId="email">
                    <Form.Control
                      type="email"
                      name="email"
                      placeholder="Email"
                      autoComplete="off"
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group controlId="mobile">
                    <Form.Control
                      type="number"
                      name="mobile"
                      placeholder="Mobile No."
                      autoComplete="off"
                    />
                  </Form.Group>
                </Col>
                <Col md={12}>
                  <Button variant="primary" type="submit">
                    Submit
                  </Button>
                </Col>
              </Row>
            </Form>
            <h6 className="mt-4">CALL US ON</h6>
            <hr>
            </hr>
            <a href='tel:02268680097'>{contact.primaryNumber}</a>
            <h6 className="mt-4">ADDRESS</h6>
            <hr>
            </hr>
            <p>{contact.address}</p>
          </Col>
        </Row>

      </Container>

      <Container fluid className="bg-dark pb-4" id="footer">
        <Row className="mx-md-5 mx-4 text-center text-white">
          <Col>
            <h6>RERA Registration Number :</h6>
            <p>The project has been registered via MahaRERA registration numbers P51800031607 and is available on the website https://maharera.mahaonline.gov.in under registered projects.</p>
            <p><span><a href="javascript:void(0);">Term Of Use</a></span><span><a href="javascript:void(0);">Privacy Policy</a></span><span><a href="javascript:void(0);">Disclaimer</a></span></p>
            <p className="mb-4">Thank you for visiting our website. We are currently in the process of revising our website in accordance under RERA. We are in the process of updating the website. The content provided on this website is for informational purposes only, and should not be construed as legal advice on any subject matter. Without any limitation or qualification, users hereby agree with this Disclaimer, when accessing or using this website.</p>
            <h6 className="text-uppercase mb-0">COPYRIGHT © 2021 PASSCODE PRIMEPARK. ALL RIGHTS RESERVED.</h6>
          </Col>
        </Row>
  </Container>

    </main>
  )
}